html {
  --main-color: #00acdc;
  font-family: Roboto, sans-serif;
  font-style: normal;
}

body {
  margin: 0 auto;
  padding: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
  position: relative;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav__list {
  display: flex;
}

.nav__item {
  list-style-type: none;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  height: 60px;
  margin-left: 20px;
  padding: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.nav__link:hover {
  color: var(--main-color);
}

.is-active {
  color: var(--main-color);
  margin-left: 0;
  font-weight: 500;
  position: relative;
}

.is-active:after {
  content: "";
  background: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

ul, li, a {
  margin: 0;
  padding: 0;
}
